section.explanation {
    padding-top: 40px;
    
    @media (max-width: $break-small) {
        padding-top: 20px;
    }

    .primary {
        color: $primary;
    }
    
    h1.intro {
        font-family: $source, $san-serif;
        font-size: 38px;
        font-weight: bold;
        line-height: 1.17;
        margin: 0;
    
        @media (max-width: $break-small) {
        font-size: 32px;
        }
        
        @media (max-width: 550px) {
            margin-bottom: 8px;
        }
    
    }
    
    h2.intro {
        font-size: 30px;
        font-weight: 400;
        font-family: $san-serif;
        line-height: 1.4;
        word-wrap: normal;
        margin: 20px 0 0 0;
    
        @media (max-width: $break-small) {
        font-size: 24px;
        }    
        
    }
    
    p.body {
        font-size: 16px;
        color: $grey-0;
        line-height: 1.75;
        margin: 20px 0 64px 0;
    }
    
}